"use client";
import { useState } from "react";
import { FaRegEdit } from "react-icons/fa";
import { MdDeleteOutline } from "react-icons/md";
export default function Users() {
  const route=user
  const [users, setUsers] = useState([
    { id: 1, fname: "", lname: "", email: "", password: "" },
    { id: 2, fname: "", lname: "", email: "", password: "" },
    { id: 3, fname: "", lname: "", email: "", password: "" },
    { id: 4, fname: "", lname: "", email: "", password: "" },
    { id: 5, fname: "", lname: "", email: "", password: "" },
  ]);

    return (
    <div className="flex justify-center items-center h-screen bg-gray-100 ">
      <div className="w-auto bg-white p-6 rounded-lg shadow-lg">
        <h1 className="text-2xl font-bold mb-4 text-center">CRUD APP</h1>

        <div className="flex justify-end mb-3">
          <button className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600" onClick={route.push("/adduser")}>
            Add User
          </button>
        </div>

        <table className="w-full border-collapse border border-gray-300">
          <thead>
            <tr className="bg-gray-200">
              <th className="border p-2">Fname</th>
              <th className="border p-2">Lname</th>
              <th className="border p-2">Email</th>
              <th className="border p-2">Password</th>
              <th className="border p-2">Actions</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user.id}>
                <td className="border p-2">
                  <input 
                    type="text" 
                    className="w-full p-1 border rounded" 
                    value={user.fname} 
                  />
                </td>
                <td className="border p-2">
                  <input 
                    type="text" 
                    className="w-full p-1 border rounded" 
                    value={user.lname} 
                  />
                </td>
                <td className="border p-2">
                  <input 
                    type="email" 
                    className="w-full p-1 border rounded" 
                    value={user.email} 
                  />
                </td>
                <td className="border p-2">
                  <input 
                    type="password" 
                    className="w-full p-1 border rounded" 
                    value={user.password} 
                  />
                </td>
                <td className="border p-2 flex space-x-2">
                  <button 
                    className="bg-green-500 text-white px-2 py-2 rounded-lg hover:bg-green-600"

                  >
                  <FaRegEdit />
                  </button>
                  <button 
                    className="bg-red-500 text-white px-2 py-2 rounded-lg hover:bg-red-600"
                  >
                 <MdDeleteOutline />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

      </div>
    </div>
  );
}
